@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

:root {
  --title-font: 'Montserrat Alternates', sans-serif;
  --text-color: black;
  --light-accent-color: #00E9AA;
  --dark-accent-color: #84FFC5;
  --accent-color: var(--dark-accent-color);

  --light-bg: white;
  --dark-bg: rgb(14, 14, 15);
  --bg: var(--light-bg);
  --cursor-color: black;
}

body {
  background: white;
  background: var(--bg);
  overflow: hidden;
}

li {
  cursor: pointer;
}

h1 {
  font-family: 'Montserrat Alternates', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-family: var(--title-font), -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 800;
  line-height: 3rem;
}

p {
  line-height: 1.6rem;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

button {
  border: none;
  padding: 12px 18px;
  background-color: #84FFC5;
  background-color: var(--accent-color);
  font-size: .9rem;
  color: black;
}


.page404 {
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.page404>img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.page404>h1 {
  z-index: 100;
  text-align: center;
  font-size: 4rem;
  padding-bottom: 10px;
}


@-webkit-keyframes fadefrombottom {

  0% {
    opacity: 0;
    transform: translateY(10px);
  }

  100% {
    opacity: 1;
    transform: translateY(10px);
  }
}


@keyframes fadefrombottom {

  0% {
    opacity: 0;
    transform: translateY(10px);
  }

  100% {
    opacity: 1;
    transform: translateY(10px);
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home {
  height: 100vh;
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 250px auto 200px;
      grid-template-columns: 250px auto 200px;
}

.nav {
  overflow: hidden;
  border-right: 2px solid whitesmoke;
  display: flex;
  flex-direction: column;
}

.nav img {
  width: 100%;
}

.nav header {
  padding: 20px;
}

.nav header img {
  width: 80px;
}

.nav .profile-preview {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
  margin-top: auto;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100%;
  display: flex;
  align-items: center;
  border-top: 1px solid whitesmoke;
  cursor: pointer;
}

.nav .profile-preview .about {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.nav .profile-preview img {
  display: block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-right: 10px;
  object-fit: cover;
}

.nav-links img {
  width: 20px;
  margin-right: 10px;
}

.nav-links a {
  width: 100%;
  height: 100%;
  padding: 10px;
  color: var(--text-color);
  display: flex;
  align-items: center;
}

.nav-links .nav-link {
  margin: 10px;
  cursor: pointer;
  transition: transform 200ms ease, background 200ms ease, font-weight 100ms ease;
}

.nav-links .nav-link:hover {
  transform: scale(1.06);
  font-weight: 600;
}

.nav-links .selected-nav-link {
  background: var(--accent-color);
  font-weight: 600;
}

.feed header {
  width: 100%;
  height: 50px;
  padding: 20px;
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  border-bottom: 2px solid whitesmoke;
}

.create-post {
  padding: 20px;
  border-bottom: 2px solid whitesmoke;
}

.create-post form {
  display: flex;
  align-items: center;
  width: 100%;
}

.create-post form > img {
  margin-bottom: auto;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  object-fit: cover;
  margin-right: 10px;
}

.create-post .compose {
  width: 100%;
}

.create-post .compose textarea {
  height: 100%;
  width: 100%;
  padding-top: 0.5rem;
  outline: none;
  border: none;
  resize: none;
  font-size: 1.2rem;
  color: var(--text-color);
}

.create-post button {
  margin-left: auto;
  width: auto;
  background: white;
  padding: 8px 20px;
  font-weight: 600;
  border: 2px solid var(--accent-color);
  transition: all 1s ease;
  -webkit-animation: fadefrombottom 100ms forwards ease;
          animation: fadefrombottom 100ms forwards ease;
}

@media only screen and (max-width: 600px) {
  .create-post button {
    width: 100%;
  }
}

.create-post .disabled-post {
  -webkit-filter: grayscale(0.6) blur(1px);
          filter: grayscale(0.6) blur(1px);
  cursor: not-allowed;
}

.activity {
  border-left: 2px solid whitesmoke;
}

@media only screen and (max-width: 700px) {
  .home {
    height: 100vh;
    width: 100%;
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 200px 1fr;
        grid-template-columns: 200px 1fr;
  }
  .feed {
    width: 100%;
  }
  .activity {
    display: none;
  }
}

@media only screen and (max-width: 510px) {
  .home {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-end;
  }
  .feed {
    width: 100%;
  }
  .nav {
    margin-top: auto;
    border-top: 2px solid whitesmoke;
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
  }
  .nav .profile-preview {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border: none;
  }
  .nav .profile-preview .about {
    display: none;
  }
  .nav header {
    display: none;
  }
  .nav .nav-links {
    display: flex;
    flex-direction: row;
    width: 100%;
  }
  .nav .nav-links .nav-link {
    justify-content: center;
  }
  .nav .nav-links img {
    margin: 0;
  }
  .nav .nav-links span {
    display: none;
  }
  .activity {
    display: none;
  }
}
